@import 'globals';

div.page {
  display: flex;
  height: 100%;
}
section {
  width: 80%;
  min-width: 250px;
}
section > h1 {
  display: inline-block;
  width: 80%;
}
section > div:not(.about-menu, .whoops-404),
section > section {
  margin: 1em;
}

section.products {
  margin: 1em;
  justify-content: center;
}
section.events,
section.contact {
  margin: 1em;
  align-items: center;
  display: flex;
  flex-direction: column;
  padding-top: 5em;
}
section > img {
  max-width: 650px;
}
h3 {
  font-size: 1.5em;
  margin-bottom: 15px;
}

h2 {
  font-size: 2em;
  margin-bottom: 15px;
}

h1 {
  font-size: 3em;
  color: #262f41;
  margin-bottom: 20px;
}

div.home {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  h1 {
    font-size: 3em;
    color: #262f41;
    margin-bottom: 20px;
  }

  > nav {
    background-color: #262f41;
    padding: 1em;
    border-top: dashed 0.1em ghostwhite;
    border-bottom: dashed 0.1em ghostwhite;
    width: calc(100% - 2em);
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

    a {
      font-size: 2em;
      color: ghostwhite;
      flex-basis: 200px;
    }
  }
}

section.about {
  > section {
    padding: 2em;
  }
}

div.whoops-404 {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  display: flex;
  width: 100%;
  height: 100%;
  margin: 0;
  justify-content: center;
  align-items: center;
  background-color: darkred;
  color: ghostwhite;
  font-size: 1.5em;

  a {
    color: white;
    text-decoration: underline;
    margin: 1em;
    &:hover {
      color: yellow;
      text-decoration: none;
    }
  }
}

.listContainer {
  width: 100%;
  min-width: 600px;
  background-color: aliceblue;
  display: inline-block;
  border-radius: 30px;
  padding: 10px;
  margin: auto;
  text-align: center;
  .inner {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    .images {
      width: 45%;
      min-width: 600px;
      margin: auto;
      float: left;
      > h3 {
        position: relative;
        align-items: center;
        text-shadow: 3px 3px 4px gray;
        color: black;
      }
    }
  }
}
.btn {
  display: flex;
  position: relative;
  flex-direction: row;
  top: -300px;
  height: 0;
  align-items: center;
  justify-content: space-between;
  text-shadow: 3px 3px 4px gray;
  color: black;
  & > h2:hover {
    cursor: pointer;
  }
}
.contents {
  align-items: flex-start;
  flex-wrap: wrap;
  width: 45%;
  padding-bottom: 70px;
  min-width: 600px;
  margin: auto;
  font-size: 1.2em;
  div {
    text-align: initial;
    flex-wrap: wrap;
    width: 100%;
    padding: 4px 0;
  }
}
.images > img,
video {
  max-height: 600px;
  min-width: 200px;
  object-fit: scale-down;
  border-radius: 15px;
}
video {
  border-radius: 15px;
}
.curr {
  max-width: 700px;
}
